import { Component } from '@angular/core';
import {
  IonicPage,
  NavController,
  Alert,
  AlertController
} from 'ionic-angular';
import { ProfileProvider } from '../../providers/profile/profile';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-perfil-publico',
  templateUrl: 'perfil-publico.html',
})
export class PerfilPublicoPage {
  public userProfile: any;
  public dataNascimento: string;
  constructor(public navCtrl: NavController,     public authProvider: AuthProvider,  public alertCtrl: AlertController,
    public profileProvider: ProfileProvider) {
    }

  ionViewDidLoad() {
      this.profileProvider.getUserProfile().on('value', userProfileSnapshot => {
      this.userProfile = userProfileSnapshot.val();
      this.dataNascimento = userProfileSnapshot.val().Nascimento;
    }); 
  }


  logOut(): void {
    this.authProvider.logoutUser().then(() => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  pushComent(valor): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Digite seu comentário",
      inputs: [
        {
          name: 'coment',
          placeholder: 'Comentário',
          value: this.userProfile.coment
        },
        {
          name: 'nota',
          placeholder: 'Avaliação',
          value: valor
        },
      ],
      buttons: [
        { text: 'Cancel' },
        {
          text: 'Save',
          handler: data => {
            this.profileProvider.pushComent(data.coment, data.nota);
          }
        }
      ]
    });
    alert.present();
  }

}


